
.swiper-container {
  height: 640px;
  color: #000;
}
.swiper-slide {
  position: relative;
  cursor: pointer;
}
.swiper-slide .swiper-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: -webkit-linear-gradient(bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.1) 40%, rgba(0,0,0,0.1) 100%);
  background-image: linear-gradient(bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.1) 40%, rgba(0,0,0,0.1) 100%);
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}
.swiper-slide p {
  width: 70%;
  word-wrap: break-word;
  position: absolute;
  text-align: center;
  bottom: 30px;
  left: 15%;
  color: #fff;
  cursor: pointer;
  font-size: 30px;
}


